import React from "react";
import { Info, Settings } from "@mui/icons-material";
import { Backdrop, Fab, FormControlLabel, FormGroup, Switch, Tooltip } from "@mui/material";

import { UiCard } from "../UiCard";

interface UiSettingsProps {
    /**
     * Whether or not the settings panel is open
     */
    show: boolean;
    /**
     * Whether or not dark mode is selected
     */
    darkMode: boolean;
    /**
     * Whether or not slack mode is selected
     */
    slackMode: boolean;
    /**
     * Toggle showing the settings panel
     */
    onSettingsToggle: () => void;
    /**
     * Toggle dark mode
     */
    onDarkModeToggle: () => void;
    /**
     * Toggle slack mode
     */
    onSlackModeToggle: () => void;
}

export const UiSettings = ({
    show,
    darkMode,
    slackMode,
    onSettingsToggle,
    onDarkModeToggle,
    onSlackModeToggle,
}: UiSettingsProps) => {
    return (
        <>
            <Fab
                color="primary"
                sx={{
                    position: 'absolute',
                    bottom: (theme) => theme.spacing(2),
                    right: (theme) => theme.spacing(2),
                }}
                onClick={onSettingsToggle}
            >
                <Settings />
            </Fab>
            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={show}
                onClick={onSettingsToggle}>
                <UiCard title='Settings'>
                    <FormGroup onClick={e => e.stopPropagation()}>
                        <FormControlLabel control={
                            <Switch checked={darkMode} onChange={onDarkModeToggle} />
                        } label="Dark Mode" />
                        <FormControlLabel control={
                            <Switch checked={slackMode} onChange={onSlackModeToggle} />
                        } label={<div>Slack Mode<Tooltip title="Enter custom emoji names"><Info fontSize="small" /></Tooltip></div>} />

                    </FormGroup>
                </UiCard>
            </Backdrop>
        </>
    );
};